<template>
  <div class="container">
    <v-data-table
      :headers="headers"
      :items="rooms"
      :rows-per-page-items="rows_per_page"
    >
      <template slot="items" slot-scope="props">
        <td>{{ props.item.name }}</td>
      </template>
    </v-data-table>
  </div>
</template>

<script>
  export default {
    name: 'RoomList',
    data () {
      return {
        rows_per_page: [5, 10, 25, { "text": "All", "value": -1 }],
        headers: [
          {
            text: 'Room number',
            align: 'center',
            sortable: true,
            value: 'name'
          }
        ],
        rooms: [
          { name: '13872814' },
          { name: '52377621' },
          { name: '86506885' },
          { name: '07960397' },
          { name: '50435252' },
          { name: '99526565' },
          { name: '40416217' },
          { name: '53173865' },
          { name: '55420393' },
          { name: '60436955' },
          { name: '08543681' },
          { name: '31954977' },
          { name: '95841589' }
        ]
      }
    }
  }
</script>

<style>
  .menu__content {
    background-color: #fff;
  }
</style>



